import axios from "axios";
import { useState, createContext, useEffect } from "react";

export const ProfileDataContext = createContext({});  


export const ProfileDataProvider = ({ children }) =>{
    const [ examNames, setExamNames ] = useState([])
    const [ profileData, setProfileData ] = useState({
        "first_name": "",
        "middle_name": "",
        "last_name": "",
        "date_of_birth": "",
        "countryCode": "",
        "mobile": "",
        "email": "",
        "target_exams": [],
        "role": 0,
        "education": [] 
    });

    const resetProfileData = () =>{
        setProfileData({
            "first_name": "",
            "middle_name": "",
            "last_name": "",
            "date_of_birth": "",
            "countryCode": "",
            "mobile": "",
            "email": "",
            "target_exams": [],
            "role": 0,
            "education": [] 
        });
    }

    useEffect(()=>{
        axios.get("/api/getTargetExams/")
              .then(resp => {
                if (resp.data["task_completed"]){
                    setExamNames(resp.data["exams"])
                }
            })
            .catch(err => {
                alert("Couldn't retrieve the target exams.")
                // setExamNames([[1, 'JEE'], [2, 'GATE'], [3, 'CAT'], [4, 'UPSC'], [5, 'CGPSC']])
              })
    },[]);

    return (
        <ProfileDataContext.Provider value={{ 
                                                profileData,
                                                setProfileData,
                                                resetProfileData,
                                                examNames
                                            }}>
            { children }
        </ProfileDataContext.Provider>
    )
}